import { Core } from '..';

import { MouseInteraction } from './EventInteraction/MouseInteraction';

export class IOWrapper {
    core: Core;
    mouseInteraction: MouseInteraction;

    constructor(core: Core) {
        this.core = core;

        this.mouseInteraction = new MouseInteraction(this);
    }
    public InitEventListener(div: HTMLDivElement) {
        this.mouseInteraction.InitNativeMouseEvent(div);
        this.Resize();
    }
    private Resize() {
        // document.onresize = ((e:UIEvent)=>( this.ReSizeWindow(e) ));
        // div.addEventListener('onresize', (e: UIEvent) =>this.ReSizeWindow(e) );
        // console.log('dddd');

        window.addEventListener('resize', (e: UIEvent) => this.ReSizeWindow(e));

    }
    // tslint:disable-next-line:no-any
    ReSizeWindow(e: any) {
        // const size = this.appCoreWrapper.model.modelViewCoreWrapper.viewWrapper.divHost;
        // this.appCoreWrapper.model.graphicsCoreWrapper.canvasWrapper.canvas.ResizeCanvas(size.clientWidth, size.clientHeight);
    }
}