import { AppDrawing } from './AppDrawing';

export class AppDrawingWrapper {
    id: string;
    app: AppDrawing;
    hostDiv: HTMLElement;
    div: HTMLElement;
    version: number = 1;
    static Init(id: string = 'SandBox') {
        new AppDrawingWrapper(id, 1);
    }
    constructor(id: string = 'SandBox', version: number = 1) {
        this.id = id;
        this.version = version;
        this.Init();
    }
    private Init() {

        switch (this.version) {
            case 1:
                this.app = new AppDrawing(this.id);
                break;
            default:
                break;
        }



    }
}

export class Solution {
    public divHost: HTMLElement;

    constructor(id: string, index = 0) {
        this.divHost = document.getElementById(id);

        const div = document.createElement('div');
        div.id ='nui-sandbox';
        div.style.width = '100%';
        div.style.height = '100%';
        this.divHost.appendChild(div);

        AppDrawingWrapper.Init(div.id);
    }
    public destroy() {
        if (this.divHost) {
            try {
                // More efficient way to remove all children
                while (this.divHost.firstChild) {
                    this.divHost.removeChild(this.divHost.firstChild);
                }
            } catch (error) {
                console.error('Error in destroy method:', error);
            }
        } else {
            console.warn('divHost is undefined in destroy method');
        }
    }
}