import { AppDrawing } from '..';
import { Canvas } from './Canvas/Canvas';
import { IOWrapper } from './IOWrapper';
import { ActiveCommandController } from './ActiveCommandController/ActiveCommandController';
import { NUI } from '../../../../../lib/NJSCore';
export class Core {
    appDrawing: AppDrawing;
    canvas: Canvas;
    nUI: NUI;
    ioWrapper: IOWrapper;
    activeCommandController: ActiveCommandController;
    constructor(appDrawing: AppDrawing, div: HTMLElement) {
        this.appDrawing = appDrawing;
        this.InitPulldown();
        this.InitCanvas(div);
        this.activeCommandController = new ActiveCommandController(this);
        this.ioWrapper = new IOWrapper(this);
    }
    private InitCanvas(div: HTMLElement) {
        this.canvas = new Canvas(this, div);
    }
    private InitPulldown() {
        this.nUI = new NUI(this.appDrawing.div);
        // this.nUI.ini
        this.BindingActiveCommandController();
    }
    public InitPost() {
        this.ioWrapper.InitEventListener(this.appDrawing.div as HTMLDivElement);
    }
    private BindingActiveCommandController() {
        this.nUI.BindingActiveCommandController('NDrawingBlock', (v: string) => this.Commander(v));
    }
    public Resize(width: number, height: number) {
        this.nUI.Resize(width, height);
    }
    private Commander(v: string) {
        console.log({v})
        this.activeCommandController.pushActionFromPulldown(v);
    }
}