import { MouseEventData } from '../../lib';
import { AppDrawing } from '..';
import { BlockImplementation } from './BlockImplementation';
export class Implementations {
    appDrawing: AppDrawing;
    implementation: BlockImplementation;
    constructor(nBlock: AppDrawing) {
        this.appDrawing = nBlock;
        this.implementation = new BlockImplementation(this);
    }
    public MouseEventController(e: MouseEventData) {
        this.implementation.MouseEventController(e);
    }
    public Render(ctx: CanvasRenderingContext2D) {
        this.implementation.Render(ctx);
    }
}