import { Deltatime, CanvasLoopController } from '../../../../../../lib/NJSCore';
import { MouseEventData } from '../../../lib';
import { Core } from '..';
export class Canvas {
    core: Core;
    div: HTMLElement;
    canvas: HTMLCanvasElement;
    ctx: CanvasRenderingContext2D;
    isActive: boolean = false;
    zoom: number = 1;
    translation: number[] = [0, 0];
    translationZoom: number[] = [0, 0];
    deltaTime: Deltatime;
    canvasLoopController: CanvasLoopController;

    log: string;
    // frameRate: number = 60;
    isViewChanging: boolean = false;
    loopNum: number = 0;
    needRenderloop: boolean = true;

    ablePan: boolean = true;
    pause: boolean = false;
    constructor(core: Core, div: HTMLElement) {
        this.core = core;
        this.div = div;
        this.Init();
        this.LoopInit();
    }
    private Init() {
        const canvas = document.createElement('canvas');
        if (canvas) {
            this.canvas = canvas;
            this.canvas.width = this.div.clientWidth;
            this.canvas.height = this.div.clientHeight;
            this.div.append(this.canvas);
            const ctx = this.canvas.getContext('2d');
            if (ctx) {
                this.ctx = ctx;
                this.isActive = true;
            }
        }
    }
    private LoopInit() {
        this.translation[0] = this.ctx.canvas.width * 0.5;
        this.translation[1] = this.ctx.canvas.height * 0.5;

        this.deltaTime = new Deltatime();
        this.canvasLoopController = new CanvasLoopController(60);
        this.SetLevel4();

        this.Loop();
    }
    public Loop() {
        let before = performance.now();
        this.deltaTime.UpdateFPS();

        if (this.canvasLoopController.level > 3) {
            if (this.canvasLoopController.level === 6) { // level 6
                requestAnimationFrame(() => { this.Loop(); });
                // this.Clear(this.ctx);
                this.RenderPre(this.ctx);
            } else if (this.canvasLoopController.level === 5) {
                if (this.isViewChanging) {
                    requestAnimationFrame(() => { this.Loop(); });
                } else {
                    // setTimeout(() => {
                    requestAnimationFrame(() => { this.Loop(); });
                    // }, 1000 / this.frameRate);
                }
                // this.Clear(this.ctx);
                this.RenderPre(this.ctx);
            } else if (this.canvasLoopController.level === 4) {
                if (this.isViewChanging) {
                    requestAnimationFrame(() => { this.Loop(); });
                } else {
                    // setTimeout(() => {
                    requestAnimationFrame(() => { this.Loop(); });
                    // }, 1000 / this.frameRate);
                }
                if (!this.canvasLoopController.stop) {
                    this.RenderPre(this.ctx);
                    this.canvasLoopController.TimerForStop();
                } else {
                    return;
                }
            }

        } else if (this.canvasLoopController.level === 1) { // level 1 - 3
            if (this.isViewChanging) {
                // this.Clear(this.ctx);
            } else {
                // this.Clear(this.ctx);
                this.RenderPre(this.ctx);
            }
        } else {
            // this.Clear(this.ctx);
            this.RenderPre(this.ctx);
        }
        let after = performance.now();
        this.log = `level:  ${this.canvasLoopController.level}, total frames: ${this.loopNum++} (FPS: ${this.deltaTime.frameRate.toFixed(0)}), a loop took: ${(after - before).toFixed(2)} ms to execute.`;
        // this.LogOut();
    }
    public SetLevel1() { this.canvasLoopController.level = 1; this.Loop(); }
    public SetLevel2() { this.canvasLoopController.level = 2; this.Loop(); }
    public SetLevel3() { this.canvasLoopController.level = 3; this.Loop(); }
    public RenderSetLevel4() {
        this.canvasLoopController.stop = true;
    }
    public SetLevel4() {
        this.canvasLoopController.Reset();
        setTimeout(() => {
            this.canvasLoopController.level = 4;
            this.Loop();
        }, 500);
    }
    public SetLevel5() {
        this.canvasLoopController.Reset();
        setTimeout(() => {
            this.canvasLoopController.level = 5;
            this.Loop();
        }, 500);
    }
    public SetLevel6() {
        this.canvasLoopController.Reset();
        setTimeout(() => {
            this.canvasLoopController.level = 6;
            this.Loop();
        }, 500);
    }
    public Clear(ctx: CanvasRenderingContext2D) {
        // this.ctx.clearRect(-this.translation[0], -this.translation[1], this.ctx.canvas.width, this.ctx.canvas.height);
        this.ctx.clearRect(0, 0, this.ctx.canvas.width, this.ctx.canvas.height);

    }
    public Zoom(e: MouseEventData) {
        // const offsetX = (e.xs);
        // const offsetY = (e.ys);
        // console.log(offsetX, this.translation[0]);

        if (e.wheel < 0) {
            this.zoom += this.zoom * 0.09;
            // console.log('in', this.zoom);
            // this.translation[0] += (this.ctx.canvas.width * 0.5 - offsetX) / this.zoom;
            // this.translation[1] += (this.ctx.canvas.height * 0.5 - offsetY) / this.zoom;
            // this.translationZoom[0] = (offsetX);
            // this.translationZoom[1] = (offsetY);
        } else {
            console.log('out', this.zoom);
            this.zoom -= this.zoom * 0.09;

            // this.translation[0] -= (this.ctx.canvas.width * 0.5 - offsetX) / this.zoom;
            // this.translation[1] -= (this.ctx.canvas.height * 0.5 - offsetY) / this.zoom;
            // this.translation[0] -= (this.translation[0]) - offsetX;
            // this.translation[1] -= (this.translation[1]) - offsetY;
            // this.translation[0] += (offsetX / this.zoom);
            // this.translation[1] += (offsetY / this.zoom);
            // this.translationZoom[0] = (offsetX);
            // this.translationZoom[1] = (offsetY);
            // this.translation[0] += offsetX / this.zoom;
            // this.translation[1] += offsetY / this.zoom;
        }

    }
    public RemapByZoom(e: MouseEventData) {
        e.x = ((e.xs - this.translation[0]) / this.zoom);
        e.y = ((e.ys - this.translation[1]) / this.zoom);
        return e;
    }
    public Pan(e: MouseEventData) {
        if (!this.ablePan) { return; }
        const xOff = e.xs - e.preXs;
        const yOff = e.ys - e.preYs;
        this.translation[0] += xOff;
        this.translation[1] += yOff;
    }
    public RenderForce() {
        this.canvasLoopController.stop = false;
    }
    public RenderPre(ctx: CanvasRenderingContext2D) {
        if (!this.isActive) { return; }
        this.Clear(this.ctx);
        this.ctx.save();
        this.ctx.translate(this.translation[0], this.translation[1]);
        this.ctx.scale(this.zoom, this.zoom);
        // this.ctx.translate(this.translationZoom[0], this.translationZoom[1]);
        // this.translationZoom[0] = this.translationZoom[1] = 0;

        this.Render(ctx);

        this.ctx.restore();
    }
    public Render(ctx: CanvasRenderingContext2D) {
        // TODO
    }
}