import { Core } from './Core';
import { Implementations } from './Implementations';
export class AppDrawing {
    id: string;
    core: Core;
    implementations: Implementations;
    hostDiv: HTMLElement;
    div: HTMLElement;
    static Init(id: string = 'SandBox') {
        new AppDrawing(id);
    }
    constructor(id: string = 'SandBox') {
        this.id = id;
        this.Init();
        this.InitPost();

        // window['app'] = this;
    }
    private Init() {
        const hostDiv = document.getElementById(this.id);
        if (hostDiv) { this.hostDiv = hostDiv; } else { return; }

        const div = document.createElement('div');
        if (div) {
            this.div = div;
            this.div.style.width = '100%';
            this.div.style.height = '100%';
            // this.div.style.background = '#dddddd';
            this.hostDiv.append(this.div);
        }

        this.core = new Core(this, div);
        this.implementations = new Implementations(this);
    }
    private InitPost() {
        this.core.InitPost();
        this.core.canvas.Render = (ctx: CanvasRenderingContext2D) => this.implementations.Render(ctx);
    }
}