import { MouseEventData } from '../../../lib';
import { Implementations } from '..';
import { ActiveCommandController } from './ActiveCommandController/ActiveCommandController';
export class BlockImplementation {
    implementations: Implementations;

    activeCommandController: ActiveCommandController;
    constructor(implementations: Implementations) {
        this.implementations = implementations;
        this.Init();
    }
    private Init() {
        this.activeCommandController = new ActiveCommandController(this);

    }
    public MouseEventController(e: MouseEventData) {
        this.activeCommandController.activeCommand.MouseEventController(e);
    }
    public Render(ctx: CanvasRenderingContext2D) {

        // CanvasDrawing.GridLineOrigin(ctx, 10, 10, 50, 50);
        // for (let i = 0; i < this.implementation.scene.listBlocks.length; ++i) {
        //     this.implementation.scene.listBlocks[i].Render(ctx);
        // }


        ctx.fillStyle = '#00ff00';
        ctx.beginPath();
        ctx.arc(this.activeCommandController.mouse.x, this.activeCommandController.mouse.y, 2, 0, Math.PI * 2);
        ctx.closePath();
        ctx.fill();

    }
}